<template>
  <div class="itemList" v-if="voice">
    <div class="list">
      <router-link
        :to="{
          path: '/play',
          query: { id: voice.voiceId, listid: $route.query.id },
        }"
        class="item"
      >
        <div class="img">
          <van-image
            :src="voice.cover"
            width="60"
            height="60"
            lazy-load
            radius="6"
            fit="cover"
          >
            <!-- <template v-slot:loading>
              <van-loading type="spinner" size="20" />
            </template> -->
          </van-image>
          <!-- <img :src="voice.cover" alt="" /> -->
          <div class="imgIcon">
            <img
              src="../assets/maskPlayIcon.png"
              alt=""
              v-if="!(maskIsPlay && voice?.voiceId === maskPlaySheet?.voiceId)"
              @click.prevent="
                maskPlay();
                showPlayBay();
                getMaskPlaySheet(voice);
                getPlayList(sheetListVoices);
              "
            />
            <img
              src="../assets/maskPauseIcon.png"
              alt=""
              v-else
              @click.prevent="
                maskUnplay();
                getMaskPlaySheet(voice);
              "
            />
          </div>
        </div>
        <div class="itemDetail">
          <div class="itemName">
            <p
              :style="{
                color: voice?.voiceId === maskPlaySheet?.voiceId ? 'red' : '',
              }"
            >
              {{ voice.name }}
            </p>
          </div>
          <div class="voiceInfo">
            <span>
              <i class="voiceTime"></i>
              {{
                Math.floor(voice.duration / 60) > 0
                  ? Math.floor(voice.duration / 60) > 9
                    ? Math.floor(voice.duration / 60)
                    : "0" + Math.floor(voice.duration / 60)
                  : "00"
              }}'{{
                voice.duration % 60 > 9
                  ? voice.duration % 60
                  : "0" + (voice.duration % 60)
              }}"
            </span>
            <span>
              <i class="voicePlay"></i>
              {{
                voice.playCount > 10000
                  ? (voice.playCount / 10000).toFixed(1) + "万"
                  : voice.playCount
              }}
            </span>
            <span>
              <i class="voiceMsg"></i>
              {{
                voice.commentCount > 10000
                  ? (voice.commentCount / 10000).toFixed(1) + "万"
                  : voice.commentCount
              }}
              <!-- {{ voice.commentCount }} -->
            </span>
          </div>
        </div>
        <div class="itemDownLoad">
          <img src="../assets/download.bd43346.png" alt="" />
        </div>
      </router-link>
    </div>

    <!-- <div class="toTopBtn">

    </div> -->
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  props: {
    voice: Object,
    sheetListVoices: Array,
  },
  computed: {
    ...mapState(["maskIsPlay", "maskPlaySheet"]),
  },
  methods: {
    ...mapMutations([
      "maskPlay",
      "maskUnplay",
      "getMaskPlaySheet",
      "getPlayList",
      "showPlayBay",
    ]),
  },
};
</script>

<style lang="less" scoped>
.itemList {
  // &:nth-child(5) {
  //   .list {
  //     .a {
  //       padding-top: 5px;
  //     }
  //   }
  // }
  .list {
    padding: 0 16px;
    background: #fff;
    a.item {
      text-decoration: none;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      padding: 15px 0;
      position: relative;
      color: #333;

      &::after {
        content: "";
        position: absolute;
        display: block;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 1px;
        background: #dbdbdb;
        z-index: 100;
        transform: scaleY(0.5);
      }
      .img {
        flex-shrink: 0;
        width: 60px;
        height: 60px;
        margin-right: 10px;
        position: relative;
        //   img {
        //     border-radius: 6px;
        //     //   width: 100%;
        //     width: 60px;
        //     height: 60px;
        //   }
        .imgIcon {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          display: flex;
          justify-content: center;
          align-items: center;
          img {
            width: 31.89px;
            height: 31.89px;
          }
        }
      }
      .itemDetail {
        width: 248px;
        //   height: 68.2px;
        //   webkit-box-flex: 1;
        //   flex: 1;
        .itemName {
          font-size: 14px;
          color: #000;
          padding-bottom: 5px;
          font-weight: 400;
          p {
            margin: 14px 0;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 2;
          }
        }
        .voiceInfo {
          font-size: 12px;
          color: #b2b2b2;
          display: flex;
          justify-content: flex-start;
          align-items: center;
          span {
            margin-right: 17px;
            display: flex;
            align-items: center;
            i {
              margin-right: 1.1px;
              width: 12px;
              height: 12.8px;
              background-size: 12px;
              background-repeat: no-repeat;
              background-position: center;
            }
            i.voiceTime {
              background-image: url("../assets/lishi.png");
            }
            i.voicePlay {
              background-image: url("../assets/play_num.png");
            }
            i.voiceMsg {
              background-image: url("../assets/ping.png");
            }
          }
        }
      }
      .itemDownLoad {
        width: 25px;
        height: 25px;
      }
    }
  }
}
</style>