<template>
  <li class="comment_item">
    <img
      :src="commentdata.photo"
      alt=""
      @click="
        $router.push({
          path: '/user',
          query: {
            userId: commentdata.userId,
          },
        })
      "
    />
    <div class="desc">
      <div
        class="name"
        @click="
          $router.push({
            path: '/user',
            query: {
              userId: commentdata.userId,
            },
          })
        "
      >
        {{ commentdata.name }}
      </div>
      <div class="content">{{ commentdata.content }}</div>
      <div class="time">{{ format(commentdata.createTime) }}</div>
    </div>
    <div class="zan">
      <div class="good" @click="localGood(commentdata.userId)">
        <i></i>
        <span v-if="commentdata.userId !== commentGood">{{
          commentdata.laudCnt
        }}</span>
        <span v-else :style="{ color: isGood ? 'red' : '' }">{{
          commentdata.laudCnt + 1
        }}</span>
      </div>
    </div>
  </li>
</template>

<script>
export default {
  props: {
    commentdata: Object,
  },
  data() {
    return {
      commentGood: localStorage?.getItem("commentGood") || null,
      isGood: localStorage?.getItem("isGood") || false,
    };
  },
  methods: {
    add: function (m) {
      return m < 10 ? "0" + m : m;
    },
    format: function (ntime) {
      var time = new Date(ntime * 1000);
      var year = time.getFullYear();
      var month = time.getMonth() + 1;
      var day = time.getDate();
      return year + "-" + this.add(month) + "-" + this.add(day);
    },
    // 点赞本地存储
    localGood: function (id) {
      // this.isGood = true;
      localStorage?.setItem("isGood", true);
      localStorage?.setItem("commentGood", id);
    },
  },
};
</script>

<style lang="less" scoped>
.comment_item {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  border-bottom: 1px solid #ddd;
  padding: 16px 0;
  img {
    display: block;
    width: 35px;
    height: 35px;
    border-radius: 50%;
    margin-right: 10px;
  }
  .desc {
    flex: 1;
    text-align: left;
    .name {
      font-size: 15px;
      color: #b2b2b2;
      padding-bottom: 8px;
    }
    .content {
      width: 250px;
      font-size: 15px;
      color: #4c4c4c;
      margin: 0;
      padding: 0 0 10px;
      word-break: break-all;
    }
    .time {
      font-size: 12px;
      color: #b2b2b2;
    }
  }
  .zan {
    .good {
      font-size: 0.24px;
      color: #b2b2b2;
      display: flex;
      align-items: center;
      i {
        display: block;
        width: 16px;
        height: 16px;
        background-image: url("../assets/点赞.png");
        background-size: 16px;
        background-repeat: no-repeat;
        background-position: center;
      }
      span {
        margin-left: 2.5px;
        font-size: 14px;
      }
    }
  }
}
</style>