<template>
  <div class="channelView">
    <header>
      <a class="icon" href="#"
        ><img src="../assets/appHeadIcon.png" alt="" class="headIcon"
      /></a>
      <div class="headInfo">
        <img src="../assets/appHeadLogoText.png" alt="" class="logoText" />
        <div class="infoText">打开APP收藏下载，精彩不容错过</div>
      </div>
      <div class="downLoadBtn top">打开APP</div>
    </header>
    <div class="headTitle">
      <router-link to="/">
        <img src="../assets/backToHome.png" alt="" />
      </router-link>
      <h1>{{ $route.query.channelName }}</h1>
    </div>
    <div class="navList">
      <router-link
        :to="{
          path: '/channel',
          query: {
            channelId: $route.query.channelId,
            channelName: $route.query.channelName,
            sceneCode: $route.query.sceneCode,
          },
        }"
        class="navItem"
        >推荐</router-link
      >
      <router-link
        :to="{
          path: '/channel/types',
          query: {
            typeName: item.channelName,
            channelId: $route.query.channelId,
            channelName: $route.query.channelName,
            sceneCode: $route.query.sceneCode,
          },
        }"
        class="navItem"
        v-for="item in navList.data"
        :key="item.channelId"
        >{{ item.channelName }}</router-link
      >
    </div>
    <router-view />
    <van-list
      v-model="loading"
      @load="onLoad"
      v-show="$route.meta.showHomeItem"
    >
      <HomeSheet
        v-for="sheet in list"
        :key="sheet?.playSheetInfo?.id"
        :sheet="sheet"
      />
    </van-list>
  </div>
</template>

<script>
import HomeSheet from "@/components/HomeSheet.vue";
import qs from "qs";
export default {
  components: {
    HomeSheet,
  },
  data() {
    return {
      navList: [],

      list: [],
      loading: false,
      finished: false,
    };
  },
  methods: {
    getChannelTag: function () {
      this.axios
        .get("https://m.lizhi.fm/vodapi/m5/channelTags", {
          params: { channelId: this.$route.query.channelId },
        })
        .then((res) => {
          // console.log(res);
          this.navList = res.data;
        });
    },
    onLoad() {
      // console.log(1);
      this.axios
        .post(
          "https://m.lizhi.fm/vodapi/m5/recommend",
          qs.stringify({
            token: "null",
            performanceId: "null",
            deviceId: "h5-4565da47-aa38-728d-dc31-4bb9922a139f",
            sceneCode: this.$route.query.sceneCode,
            bizCode: "playlist",
            refresh: 1,
            channelId: this.$route.query.sceneCode,
          })
        )

        .then((res) => {
          this.list = [...this.list, ...res.data.data?.cardPlayBeanList];
          this.loading = false;
        });
    },
  },
  beforeRouteUpdate(to, from, next) {
    if (!this.list.length) {
      this.onLoad();
    }
    next();
  },
  mounted: function () {
    this.getChannelTag();
  },
};
</script>

<style lang="less" scoped>
.channelView {
  header {
    position: fixed;
    top: 0;
    .top {
      letter-spacing: normal;
    }
  }
  .headTitle {
    height: 48px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    a {
      height: 38px;
      display: flex;
      justify-content: flex-end;
      align-items: center;
      width: 40px;
      text-decoration: none;
      color: #333;
      img {
        width: 25px;
      }
    }
    h1 {
      flex: 1;
      font-size: 16px;
      text-align: left;
      font-weight: 700;
      padding-left: 120px;
    }
  }
  .navList {
    width: auto;
    height: 40px;
    display: block;
    overflow-x: scroll;
    overflow-y: hidden;
    white-space: nowrap;
    position: sticky;
    top: 58px;
    z-index: 999;
    border-bottom: 1px solid #eee;
    background: #fff;

    display: flex;
    justify-content: space-between;
    a.navItem {
      // color: #000;
      // position: relative;
      flex: 1;
      color: rgba(0, 0, 0, 0.3);
      padding: 0 13.5px;
      height: 40px;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      font-size: 14px;
      text-decoration: none;
      &.router-link-exact-active {
        color: #000;
        position: relative;
        &::after {
          content: "";
          display: block;
          z-index: 1000;
          position: absolute;
          bottom: 0;
          left: 50%;
          margin-left: -6px;
          width: 12px;
          height: 3px;
          border-radius: 1.5px;
          background: #fe5353;
        }
      }
    }
    &::-webkit-scrollbar {
      display: none;
    }
  }
}
</style>