<template>
  <div class="home">
    <div class="header">
      <h3>听世界</h3>
    </div>
    <div class="headerTagList" v-show="!$store.state.getTagMore">
      <div
        class="headerTagItem"
        v-for="tag in homeHederTag.slice(1, 10)"
        :key="tag.channelId"
      >
        <router-link
          :to="{
            path: '/channel',
            query: {
              channelId: tag.channelId,
              channelName: tag.channelName,
              sceneCode: tag.sceneCode,
            },
          }"
        >
          <div class="tag">
            <van-image width="55" :src="tag.channelUrl" fit="cover" />
            <!-- <img :src="tag.channelUrl" alt="" /> -->
            <h5>{{ tag.channelName }}</h5>
          </div>
        </router-link>
      </div>
      <div class="tagMore" @click="toggleGetTag">
        <img src="../assets/tagDown.png" alt="" />
        <h5>更多</h5>
      </div>
    </div>
    <div class="headerTagList" v-show="$store.state.getTagMore">
      <div
        class="headerTagItem"
        v-for="tag in homeHederTag.slice(1, homeHederTag.length)"
        :key="tag.channelId"
      >
        <router-link
          :to="{
            path: '/channel',
            query: {
              channelId: tag.channelId,
              channelName: tag.channelName,
              sceneCode: tag.sceneCode,
            },
          }"
        >
          <div class="tag">
            <van-image width="55" :src="tag.channelUrl" fit="cover" />
            <!-- <img :src="tag.channelUrl" alt="" /> -->
            <h5>{{ tag.channelName }}</h5>
          </div>
        </router-link>
      </div>
      <div class="tagMore" @click="toggleGetTag">
        <img src="../assets/tagUp.png" alt="" />
        <h5>收起</h5>
      </div>
    </div>
    <van-list v-model="loading" @load="onLoad">
      <!-- <van-cell v-for="item in list" :key="item" :title="item" /> -->
      <HomeSheet
        v-for="sheet in list"
        :key="sheet.playSheetInfo.id"
        :sheet="sheet"
      ></HomeSheet>
    </van-list>
  </div>
</template>

<script>
// @ is an alias to /src
import HomeSheet from "@/components/HomeSheet.vue";
import { mapState, mapMutations } from "vuex";
export default {
  components: {
    HomeSheet,
  },
  data() {
    return {
      // ...mapState(["homeHederTag"]),
      list: this.$store.state.homeSheet,
      loading: false,
      // finished: false,
    };
  },
  computed: {
    ...mapState(["homeHederTag", "homeSheet"]),
  },
  methods: {
    ...mapMutations(["getHomeHeaderTag", "toggleGetTag", "getHomeSheet"]),
    onLoad() {
      // 异步更新数据;
      this.axios
        .post("https://m.lizhi.fm/vodapi/playSheetRecommend/recommend")
        .then((res) => {
          this.getHomeSheet(res.data.data.cardPlayBeanList);
          this.list = this.list.concat(this.homeSheet);
          // console.log(this.list);
          this.loading = false;
          // 数据全部加载完成
          // if (this.list.length >= 40) {
          //   this.finished = true;
          // }
        });
    },
  },
  created: function () {
    // this.loading = true;
    this.axios.get("https://m.lizhi.fm/vodapi/m5/channel").then((res) => {
      // console.log(res);
      this.getHomeHeaderTag(res.data.data);
      // console.log(res.data.data, this.$store.state.homeHederTag,);
    });
  },
};
</script>

