<template>
  <div class="userView" v-if="userInfo">
    <header>
      <a class="icon" href="#"
        ><img src="../assets/appHeadIcon.png" alt="" class="headIcon"
      /></a>
      <div class="headInfo">
        <img src="../assets/appHeadLogoText.png" alt="" class="logoText" />
        <div class="infoText">打开APP收听更多热门声音</div>
      </div>
      <div class="downLoadBtn top">打开APP</div>
    </header>
    <div class="userInfo">
      <div class="headImg">
        <van-image
          :src="userInfo.cdnPortrait + userInfo.user.portrait"
          width="108"
          height="108"
          lazy-load
          radius="50%"
          fit="cover"
        >
          <!-- <template v-slot:loading>
            <van-loading type="spinner" size="20" />
          </template> -->
        </van-image>
        <!-- <img :src="userInfo.cdnPortrait + userInfo.user.portrait" alt="" /> -->
      </div>
      <div class="userName">
        <div class="name">{{ userInfo.user.name }}</div>
        <div class="sexTag">
          <img
            src="../assets/genderMan.png"
            alt=""
            v-if="userInfo.user.gender === 1"
          />
          <img src="../assets/genderWoman.png" alt="" v-else />
        </div>
        <div class="addFocus">
          <span>
            <i></i>
            关注
          </span>
        </div>
      </div>
      <div class="userDesc">
        <span>FM {{ userInfo.user.band }}</span>
        <span>
          {{
            userInfo.user.fansCount > 10000
              ? (userInfo.user.fansCount / 10000).toFixed(1) + "万"
              : userInfo.user.fansCount
          }}
          粉丝</span
        >
        <span>
          {{
            userInfo.radio.play_cnt > 10000
              ? (userInfo.radio.play_cnt / 10000).toFixed(1) + "万"
              : userInfo.radio.play_cnt
          }}播放</span
        >
      </div>
      <div class="userDetail">
        <div class="detail" :class="{ actived }">
          <i
            @click="actived = !actived"
            v-if="userInfo.user.signature.length > 20"
          ></i>
          <div class="content">
            {{ userInfo.user.signature }}
          </div>
        </div>
      </div>
    </div>

    <UserVoiceList
      :userHotVoice="userHotVoice"
      :userPlayList="userPlayList"
    ></UserVoiceList>

    <div class="title" v-if="userAllVoice?.total">
      全部声音({{ userAllVoice?.total }})
    </div>
    <div class="list">
      <van-list v-model="loading" @load="onLoad" :finished="finished">
        <!-- v-for="allVoice in userAllVoice.audios" :key="allVoice.id" -->

        <UserAllVoiceList
          v-for="allVoice in userAllVoice?.audios"
          :key="allVoice.id"
          :allVoice="allVoice"
          :userAllVoice="userAllVoice?.audios"
          :imgUrl="userInfo.cdnAudioCover"
        ></UserAllVoiceList>
        <div class="inBottom" v-show="finished">别拉了，到底啦o(╥﹏╥)o</div>
        <!-- <van-cell v-for="item in list" :key="item" :title="item" /> -->
      </van-list>
    </div>
  </div>
</template>

<script>
import UserVoiceList from "@/components/UserVoiceList.vue";
import UserAllVoiceList from "@/components/UserAllVoiceList.vue";
import { mapState, mapMutations } from "vuex";
export default {
  components: {
    UserVoiceList,
    UserAllVoiceList,
  },
  data() {
    return {
      // userInfo: null,
      actived: false,
      userHotVoice: [],
      userPlayList: [],
      page: 0,
      userAllVoice: null,

      // vant库 List 状态
      // list: [],
      loading: false,
      finished: false,
    };
  },
  computed: {
    ...mapState(["userInfo"]),
  },
  methods: {
    ...mapMutations(["getUserInfo"]),
    // 获取用户信息
    getAxiosUserInfo: function () {
      this.axios
        .get(`https://m.lizhi.fm/api/user/info/${this.$route.query.userId}`)
        .then((res) => {
          // this.userInfo = res.data;
          this.getUserInfo(res.data);
          // console.log(res.data);
        });
    },
    // 获取用户热门声音
    getUserHotVoice: function () {
      this.axios
        .get(`https://m.lizhi.fm/vodapi/voice/hot/${this.$route.query.userId}`)
        .then((res) => {
          // console.log(res);
          this.userHotVoice = res.data.data;
        });
    },
    // 获取用户播单
    getUserPlayList: function () {
      this.axios
        .get("https://m.lizhi.fm/vodapi/playsheet/listdata", {
          params: { userId: this.$route.query.userId },
        })
        .then((res) => {
          // console.log(res);
          this.userPlayList = res.data.data;
        });
    },
    // 获取用户全部声音
    getUserAllVoiceList: function () {
      this.axios
        .get(
          `https://m.lizhi.fm/api/user/audios/${this.$route.query.userId}/${this.page}`
        )
        .then((res) => {
          // console.log(res);
          if (this.page < 2) {
            this.userAllVoice = res.data;
          }
          if (this.page > 1) {
            this.userAllVoice.audios = this.userAllVoice.audios.concat(
              res.data.audios
            );
          }
          // 加载状态结束
          this.loading = false;
        });
    },
    onLoad() {
      // 异步更新数据
      this.page += 1;
      this.getUserAllVoiceList();
      // 数据全部加载完成
      if (this?.userAllVoice?.audios?.length) {
        if (this.userAllVoice.audios.length === this.userAllVoice.total) {
          this.finished = true;
        }
      }
      if (this?.userAllVoice?.audios?.length === 0) {
        this.finished = true;
      }
    },
  },
  mounted() {
    // console.log(this.$route.query.userId);
    this.getAxiosUserInfo();
    this.getUserHotVoice();
    this.getUserPlayList();
    // this.getUserAllVoiceList();
  },
};
</script>

<style lang="less" scoped>
.userView {
  background: #f2f2f2;

  header {
    position: fixed;
    top: 0;
    .top {
      letter-spacing: normal;
    }
  }
  .userInfo {
    background: #fff;
    padding-top: 15px;
    .headImg {
      // margin-top: 16px;
      margin: 16px 0;
      display: flex;
      justify-content: center;
      align-items: center;
      img {
        width: 108px;
        height: 108px;
        border-radius: 50%;
      }
    }
    .userName {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0 16px;
      height: 25px;
      .name {
        font-size: 18px;
        font-weight: 700;
        padding-right: 6px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }
      .sexTag {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        height: 25px;
        flex: 1;
        img {
          width: 20px;
          margin-right: 6px;
        }
      }
      .addFocus {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 62px;
        height: 29px;
        border-radius: 14.5px;
        border: 1px solid #fe5353;
        font-size: 12.5px;
        background: #fe5353;
        color: #fff;
        font-weight: 700;
        box-shadow: 0 2px 4px 0 rgb(255 98 98 / 30%);
        span {
          display: flex;
          justify-content: center;
          align-items: center;
          i {
            width: 12.5px;
            height: 12.5px;
            background-repeat: no-repeat;
            background-position: center;
            background-size: 9px;
            background-image: url("../assets/addFocus.png");
          }
        }
      }
    }
    .userDesc {
      padding: 8px 16px;
      color: rgba(0, 0, 0, 0.5);
      font-size: 13px;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      line-height: 1;
      span {
        margin-right: 5px;
      }
    }
    .userDetail {
      padding: 0 16px 8px;
      .detail {
        width: 100%;
        position: relative;
        margin: 0;
        &.actived {
          margin: 14px 0;
          i {
            transform: rotate(0deg);
          }
          .content {
            white-space: pre-wrap;
            word-wrap: break-word;
          }
        }
        i {
          position: absolute;
          display: block;
          top: 0;
          right: 0;
          width: 20px;
          height: 20px;
          background-image: url("../assets/top.png");
          background-size: 12px;
          background-position: center;
          background-repeat: no-repeat;
          transform: rotate(180deg);
        }
        .content {
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          font-size: 13px;
          color: #999;
          line-height: 1.8;
          padding-right: 25px;
          margin: 0;
        }
      }
    }
  }
  .title {
    padding-left: 16px;
    font-size: 14px;
    color: #333;
    transform: translateY(24px);
    position: absolute;
  }
  .list {
    width: 100%;
    padding: 0 16px;
    background: #fff;
  }
  .inBottom {
    width: 100%;
    height: 100px;
    background: #fff;
    color: #666;
    font-size: 14px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
</style>