<template>
  <div id="app">
    <header v-if="$route.meta.showNav">
      <a class="icon" href="/"
        ><img src="./assets/appHeadIcon.png" alt="" class="headIcon"
      /></a>
      <div class="headInfo">
        <img src="./assets/appHeadLogoText.png" alt="" class="logoText" />
        <div class="infoText">超两亿年轻用户都在玩的声音APP</div>
      </div>
      <div class="downLoadBtn">下载APP</div>
    </header>
    <div class="zhanweiBox"></div>

    <audio
      :src="maskPlaySheet?.voiceTrack"

      ref="audio"
      @pause="maskUnplay()"
      @ended="changeVoice('next')"
      @timeupdate="getcurrentTime($event.target.currentTime)"
      v-if="maskPlaySheet?.voiceTrack"
    ></audio>

    <audio
      :src="maskPlaySheet?.url"

      ref="audio"
      @pause="maskUnplay()"
      @ended="changeVoice('next')"
      @timeupdate="getcurrentTime($event.target.currentTime)"
      v-else-if="maskPlaySheet?.url"
    ></audio>

    <audio
      :src="maskPlaySheet?.voicePlayProperty.trackUrl"

      ref="audio"
      @pause="maskUnplay()"
      @ended="changeVoice('next')"
      @timeupdate="getcurrentTime($event.target.currentTime)"
      v-else-if="maskPlaySheet?.voicePlayProperty"
    ></audio>
    <PlayBar v-if="isShowPlayBar"></PlayBar>
    <!-- <nav>
      <router-link to="/"></router-link>
    </nav> -->
    <div class="to_top" v-show="showtop" @click="backTop">
      <i></i>
    </div>
    <router-view />
  </div>
</template>

<script>
import PlayBar from "@/components/PlayBar.vue";
import { mapState, mapMutations } from "vuex";

export default {
  components: {
    PlayBar,
  },
  data() {
    return {
      // songcurrentTime: 0,
      showtop: false,
    };
  },
  computed: {
    ...mapState([
      "isShowPlayBar",
      "maskPlaySheet",
      "maskIsPlay",
      // "duration",
      "currentTime",
    ]),
  },

  methods: {
    ...mapMutations([
      "maskPlay",
      "maskUnplay",
      // "getduration",
      "getcurrentTime",
      "changeVoice",
    ]),
    scrollToTop() {
      var scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      // console.log(document.documentElement);
      // console.log("距离顶部的距离:" + scrollTop);
      if (scrollTop > 180) {
        this.showtop = true;
      } else {
        this.showtop = false;
      }
    },
    backTop() {
      var timer = setInterval(function () {
        let scrollTop =
          document.documentElement.scrollTop || document.body.scrollTop;
        let speed = Math.floor(-scrollTop / 5);
        document.documentElement.scrollTop = document.body.scrollTop =
          scrollTop + speed;
        if (scrollTop === 0) {
          clearInterval(timer);
        }
      }, 30);
    },
  },
  mounted() {
    // this.getItemData();
    window.addEventListener("scroll", this.scrollToTop);
  },
  destroyed() {
    window.removeEventListener("scroll", this.scrollToTop);
  },
  watch: {
    maskIsPlay: function () {
      // console.log(this.maskIsPlay);
      // getcurrentTime(this.$refs.audio)
      // this.$refs.audio.load();
      if (this.maskIsPlay) {
        // this.$refs.audio.play().then(() => {});
        this.$nextTick(function () {
          this.$refs.audio
            .play()
            .then(() => {
              this.$refs.audio.play();
            })
            .catch(() => {
              this.$refs.audio.play();
            });
        });
      } else {
        this.$nextTick(function () {
          this.$refs.audio.pause();
          // this.$refs.audio.play();
        });
      }
    },
    // currentTime: function () {
    //   // if (this.songcurrentTime) {
    //   console.log(this.currentTime);
    //   // }
    // },
  },
};
</script>


