import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);



// import { Lazyload } from 'vant';
// Vue.use(Lazyload);
// Vue.use(Lazyload, {
//   lazyComponent: true,
// });

export default new Vuex.Store({
  state: {
    // 主页头部
    homeHederTag: [],
    getTagMore: false,
    // 主页推荐
    homeSheet: [],
    maskIsPlay: false,
    maskPlaySheet: null,
    isShowPlayBar: false,
    // sheetPlay: false,

    // 音频播放进度
    currentTime: 0,
    // 音频播放列表
    playlist: [],
    // 列表ID
    listid: 0,
    // 用户信息
    userInfo: null,

  },
  getters: {

  },
  mutations: {
    getHomeHeaderTag(state, res) {
      state.homeHederTag = res;
    },
    toggleGetTag(state) {
      state.getTagMore = !state.getTagMore;
    },
    getHomeSheet(state, res) {
      state.homeSheet = res;
    },
    maskPlay(state) {
      state.maskIsPlay = true;
    },
    maskUnplay(state) {
      state.maskIsPlay = false;
    },
    getMaskPlaySheet(state, sheet) {
      // console.log(sheet);
      state.maskPlaySheet = sheet;
    },
    showPlayBay(state) {
      state.isShowPlayBar = true;
    },
    //获取音频列表
    getPlayList(state, list) {

      // localStorage.setItem("playlist", JSON.stringify(list));
      state.playlist = list;

      // console.log(list);
    },
    // 获取列表ID
    getPlayListId(state, id) {
      // console.log(id);
      state.listid = id;
    },
    // 获取音频当前播放时间进度
    getcurrentTime(state, time) {
      // localStorage.setItem("li_currentTime", JSON.stringify(time));
      state.currentTime = time;
    },
    //换歌
    changeVoice(state, direction) {
      if (state.maskPlaySheet.voiceInfo) {
        let playIndex = state.playlist.findIndex(
          e => e.voiceInfo.voiceId === state.maskPlaySheet.voiceInfo.voiceId
        );
        if (direction === "next") {
          state.maskPlaySheet = playIndex === state.playlist.length - 1 ? state.playlist[0] : state.playlist[playIndex + 1];
        } else {
          state.maskPlaySheet = playIndex === 0 ? state.playlist[state.playlist.length - 1] : state.playlist[playIndex - 1];
        }
      } else {
        let playIndex = state.playlist.findIndex(
          e => e.voiceId === state.maskPlaySheet.voiceId
        );
        if (direction === "next") {
          state.maskPlaySheet = playIndex === state.playlist.length - 1 ? state.playlist[0] : state.playlist[playIndex + 1];
        } else {
          state.maskPlaySheet = playIndex === 0 ? state.playlist[state.playlist.length - 1] : state.playlist[playIndex - 1];
        }
      }
      state.maskIsPlay = true;
      // localStorage.setItem("maskPlaySheet", JSON.stringify(state.maskPlaySheet));
    },
    //获取用户信息
    getUserInfo(state, user) {
      state.userInfo = user;
    },
    changecurrentTime(state, time) {
      state.currentTime = Number(time);
      document.querySelector("audio").currentTime = state.currentTime;
      // localStorage.setItem("li_currentTime", JSON.stringify(state.currentTime));
      // return document.querySelector("audio").currentTime = state.currentTime;
    },
    // 15s后退
    timeprev(state) {
      if (state.currentTime < 15) {
        // state.currentTime = 0;
        document.querySelector('audio').currentTime = 0;
      } else {
        document.querySelector('audio').currentTime -= 15;
        // state.currentTime -= 15;
      }
      // localStorage.setItem("li_currentTime", JSON.stringify(state.currentTime));
      // return document.querySelector("audio").currentTime = state.currentTime;
    },
    // 15s前进
    timenext(state) {
      if (state.duration - state.currentTime < 15) {
        // state.currentTime = state.duration;
        document.querySelector('audio').currentTime = state.duration;
      } else {
        // state.currentTime += 15;
        document.querySelector('audio').currentTime += 15;
      }
      // localStorage.setItem("li_currentTime", JSON.stringify(state.currentTime));
      // return document.querySelector("audio").currentTime = state.currentTime;
    },
  },
  actions: {
  },
  modules: {
  }
});
