<template>
  <li class="typeList">
    <!-- @click.prevent="
      maskPlay();
      showPlayBay();
      getMaskPlaySheet(typedata);
      getPlayList(list);
    " -->
    <!-- listid: $route.query.listid, -->
    <template v-if="typedata.btnTitle">
      <div class="imgBox">
        <!-- <img :src="typedata.voiceInfo.imageUrl" alt="" /> -->
        <van-image
          width="60"
          height="60"
          lazy-load
          fit="cover"
          radius="8"
          :src="typedata.voiceInfo.imageUrl"
        />
        <div class="imgIcon">
          <img
            src="../assets/maskPlayIcon.png"
            alt=""
            v-if="
              !(
                maskIsPlay &&
                typedata?.voiceInfo?.voiceId ===
                  maskPlaySheet?.voiceInfo?.voiceId
              )
            "
            @click.prevent="
              maskPlay();
              showPlayBay();
              getMaskPlaySheet(typedata);
            "
          />
          <!-- getPlayList(sheetListVoices); -->
          <img
            src="../assets/maskPauseIcon.png"
            alt=""
            v-else
            @click.prevent="
              maskUnplay();
              getMaskPlaySheet(typedata);
            "
          />
        </div>
      </div>
      <div class="info-desc">
        <h3
          class="dots"
          :style="{
            color:
              typedata?.voiceInfo?.voiceId === maskPlaySheet?.voiceInfo?.voiceId
                ? 'red'
                : '',
          }"
        >
          {{ typedata.voiceInfo.name }}
        </h3>
        <p class="dots">{{ typedata.userInfo.name }}</p>
        <p class="opt">
          <span
            ><i></i
            >{{
              Math.floor(typedata.voiceInfo.duration / 60) > 0
                ? Math.floor(typedata.voiceInfo.duration / 60) > 9
                  ? Math.floor(typedata.voiceInfo.duration / 60)
                  : "0" + Math.floor(typedata.voiceInfo.duration / 60)
                : "00"
            }}'{{
              typedata.voiceInfo.duration % 60 > 9
                ? typedata.voiceInfo.duration % 60
                : "0" + (typedata.voiceInfo.duration % 60)
            }}"</span
          >
          <span
            ><i></i
            >{{
              typedata.voiceExProperty.replayCount > 10000
                ? (typedata.voiceExProperty.replayCount / 10000).toFixed(1) +
                  "万"
                : typedata.voiceExProperty.replayCount
            }}</span
          >
          <span><i></i>{{ typedata.voiceExProperty.commentCount }}</span>
        </p>
      </div>
    </template>
    <template v-else>
      <div class="pic_wrap">
        <img :src="typedata.cover" alt="" />
        <div class="pic_play"></div>
        <!-- :style="
            playing && playvoice.voiceId === typedata.voiceId
              ? `background-image:url('../assets/paused.png')`
              : ''
          " -->
      </div>
      <div class="info-desc">
        <h3 class="dots" style="margin: 14px 0">{{ typedata.name }}</h3>
        <!-- <p class="dots">{{ typedata.userInfo.name }}</p> -->
        <p class="opt">
          <span
            ><i></i
            >{{
              Math.floor(typedata.duration / 60) > 0
                ? Math.floor(typedata.duration / 60) > 9
                  ? Math.floor(typedata.duration / 60)
                  : "0" + Math.floor(typedata.duration / 60)
                : "00"
            }}'{{
              typedata.duration % 60 > 9
                ? typedata.duration % 60
                : "0" + (typedata.duration % 60)
            }}"</span
          >
          <span
            ><i></i
            >{{
              typedata.playCount > 10000
                ? (typedata.playCount / 10000).toFixed(1) + "万"
                : typedata.playCount
            }}</span
          >
          <span><i></i>{{ typedata.commentCount }}</span>
        </p>
      </div>
      <div class="download">
        <img src="../assets/download.bd43346.png" alt="" />
      </div>
    </template>
  </li>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  props: {
    list: Array,
    typedata: Object,
  },
  computed: {
    ...mapState(["maskIsPlay", "maskPlaySheet"]),
  },
  methods: {
    ...mapMutations([
      "maskPlay",
      "maskUnplay",
      "getMaskPlaySheet",
      "getPlayList",
      "showPlayBay",
    ]),
  },
};
</script>

<style lang="less" scoped>
li {
  width: 100%;
  padding: 12px 16px;
  height: 84px;
  display: flex;
  align-items: center;
  justify-content: flex-start;

  .imgBox {
    position: relative;
    width: 60px;
    height: 60px;
    border-radius: 8px;
    margin-right: 16px;
    flex-shrink: 0;
    img {
      width: 60px;
      height: 60px;
      // border-radius: 8px;
      // margin-right: 16px;
    }
    .imgIcon {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      img {
        width: 31.89px;
        height: 31.89px;
      }
    }
  }
  .pic_wrap {
    flex-shrink: 0;
    width: 60px;
    height: 60px;
    border-radius: 8px;
    margin-right: 16px;
    position: relative;
    img {
      display: block;
      width: 100%;
      height: 100%;
      border-radius: 8px;
    }
    .pic_play {
      width: 32px;
      height: 32px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      // background-image: url("../assets/play.png");
      background-size: 30px;
      background-repeat: no-repeat;
      background-position: center;
    }
  }
  .info-desc {
    flex: 1;
    width: 200px;
    // background-color: rgba(0,0,0,.3);
    .dots {
      display: block;
      width: 100%;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
    h3.dots {
      font-size: 14px;
      color: rgba(0, 0, 0, 0.8);
    }
    p.dots {
      margin: 4px 0;
      font-size: 10px;
      color: rgba(0, 0, 0, 0.5);
    }
    .opt {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      margin-top: 5px;
      color: rgba(0, 0, 0, 0.3);
      margin: 4px 0;
      font-size: 12px;
      span {
        margin-right: 12px;
        display: flex;
        justify-content: flex-start;
        align-items: flex-end;
        i {
          display: block;
          width: 16px;
          height: 16px;
          position: relative;
          top: 1px;
          margin-right: 3px;
          background-repeat: no-repeat;
          background-position: center;
          background-size: 11px;
        }
        &:nth-of-type(1) {
          i {
            background-image: url("../assets/lishi.png");
          }
        }
        &:nth-of-type(2) {
          i {
            background-image: url("../assets/play_num.png");
          }
        }
        &:nth-of-type(3) {
          i {
            background-image: url("../assets/ping.png");
          }
        }
      }
    }
  }
  .download {
    flex-shrink: 0;
    img {
      margin: 0;
      width: 25px;
      height: 25px;
    }
  }
}
</style>