<template>
  <li v-if="typedata">
    <div class="pic_wrap">
      <img :src="typedata.cover" alt="" />
      <div class="pic_play">
        <img
          src="../assets/maskPlayIcon.png"
          alt=""
          v-if="!(maskIsPlay && typedata?.voiceId === maskPlaySheet?.voiceId)"
          @click.prevent="
            maskPlay();
            showPlayBay();
            getMaskPlaySheet(typedata);
          "
        />
        <!-- getPlayList(sheetListVoices); -->
        <img
          src="../assets/maskPauseIcon.png"
          alt=""
          v-else
          @click.prevent="
            maskUnplay();
            getMaskPlaySheet(typedata);
          "
        />
      </div>
    </div>
    <div class="info-desc">
      <h3
        class="dots"
        :style="{
          color: typedata?.voiceId === maskPlaySheet?.voiceId ? 'red' : '',
        }"
      >
        {{ typedata.name }}
      </h3>
      <p
        class="dots"
        :style="{
          color: typedata?.voiceId === maskPlaySheet?.voiceId ? 'red' : '',
        }"
      >
        {{ typedata.userName }}
      </p>
      <p class="opt">
        <span
          >{{
            Math.floor(typedata.duration / 60) > 0
              ? Math.floor(typedata.duration / 60) > 9
                ? Math.floor(typedata.duration / 60)
                : "0" + Math.floor(typedata.duration / 60)
              : "00"
          }}'{{
            typedata.duration % 60 > 9
              ? typedata.duration % 60
              : "0" + (typedata.duration % 60)
          }}"</span
        >
        <span
          >{{
            typedata.playCount > 10000
              ? (typedata.playCount / 10000).toFixed(1) + "万"
              : typedata.playCount
          }}播放</span
        >
        <span>评论{{ typedata.commnetCount }}</span>
        <i>{{ typedata.label }}</i>
      </p>
    </div>
  </li>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  props: {
    typedata: Object,
    index: Number,
  },
  computed: {
    ...mapState(["maskIsPlay", "maskPlaySheet"]),
  },
  methods: {
    ...mapMutations([
      "maskPlay",
      "maskUnplay",
      "getMaskPlaySheet",
      "getPlayList",
      "showPlayBay",
    ]),
  },
};
</script>

<style lang="less" scoped>
li {
  width: 100%;
  padding: 12px 16px;
  height: 84px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  background-color: #fff;
  .pic_wrap {
    flex-shrink: 0;
    width: 60px;
    height: 60px;
    border-radius: 8px;
    margin-right: 16px;
    position: relative;
    img {
      display: block;
      width: 100%;
      height: 100%;
      border-radius: 8px;
    }
    .pic_play {
      width: 32px;
      height: 32px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      //   background-image: url("../assets/play.png");
      background-size: 30px;
      background-repeat: no-repeat;
      background-position: center;
    }
  }

  .info-desc {
    flex: 1;
    width: 200px;
    // background-color: rgba(0,0,0,.3);
    .dots {
      display: block;
      width: 100%;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
    h3.dots {
      font-size: 14px;
      color: rgba(0, 0, 0, 0.8);
    }
    p.dots {
      margin: 4px 0;
      font-size: 10px;
      color: rgba(0, 0, 0, 0.5);
    }
    .opt {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      //   margin-top: 5px;
      //   color: rgba(0, 0, 0, 0.3);
      margin: 4px 0;
      position: relative;
      font-size: 12px;

      color: #b2b2b2;

      span {
        margin-right: 12px;
        display: flex;
        justify-content: flex-start;
        align-items: flex-end;
      }
      i {
        position: absolute;
        right: 0;
        display: block;
        font-style: normal;
        background: #f3f3f3;
        color: #333;
        justify-self: flex-end;
        border-radius: 10px;
        padding: 0 6px;
        display: flex;
        font-size: 11.5px;
        justify-content: center;
        align-items: center;
        height: 20px;
      }
    }
  }
  .download {
    flex-shrink: 0;
    img {
      margin: 0;
      width: 25px;
      height: 25px;
    }
  }
}
</style>