<template>
  <div
    class="playBar"
    @click="
      $router.push({
        path: '/play',
        query: { id: maskPlaySheet.voiceId, listid: listid },
      })
    "
  >
    <!-- <van-circle
      v-model="currentRate"
      :rate="currentTime / maskPlaySheet.duration * 100"
      size="57.8px"
      :speed="1"
      color="#ff0c0c"
    /> -->
    <div
      class="img"
      :class="{ active: maskIsPlay }"
      v-if="maskPlaySheet.voiceTrack"
    >
      <van-image
        :src="maskPlaySheet.cover"
        width="100%"
        lazy-load
        height="100%"
        fit="cover"
      >
        <!-- <template v-slot:loading>
          <van-loading type="spinner" size="20" />
        </template> -->
      </van-image>
    </div>
    <div
      class="img"
      :class="{ active: maskIsPlay }"
      v-else-if="maskPlaySheet?.voiceInfo?.imageUrl"
    >
      <van-image
        :src="maskPlaySheet?.voiceInfo?.imageUrl"
        width="100%"
        lazy-load
        height="100%"
        fit="cover"
      >
        <!-- <template v-slot:loading>
          <van-loading type="spinner" size="20" />
        </template> -->
      </van-image>
    </div>
    <div class="img" :class="{ active: maskIsPlay }" v-else>
      <van-image
        :src="
          maskPlaySheet.cover
            ? userInfo.cdnAudioCover + maskPlaySheet.cover
            : userInfo.cdnPortrait + userInfo.user.portrait
        "
        width="100%"
        height="100%"
        fit="cover"
        lazy-load
      >
        <!-- <template v-slot:loading>
          <van-loading type="spinner" size="20" />
        </template> -->
      </van-image>
    </div>
    <!-- <img
      :src="maskPlaySheet.cover"
      alt=""
      :class="{ active: maskIsPlay }"
      v-if="maskPlaySheet.voiceTrack"
    /> -->
    <!-- <img
      :src="maskPlaySheet.voiceInfo.imageUrl"
      alt=""
      :class="{ active: maskIsPlay }"
      v-else-if="maskPlaySheet.voiceInfo.imageUrl"
    /> -->
    <!-- <img
      :src="
        maskPlaySheet.cover
          ? userInfo.cdnAudioCover + maskPlaySheet.cover
          : userInfo.cdnPortrait + userInfo.user.portrait
      "
      alt=""
      :class="{ active: maskIsPlay }"
      v-else
    /> -->
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      // currentRate: this.currentTime,
    };
  },
  computed: {
    ...mapState([
      "maskIsPlay",
      "maskPlaySheet",
      "currentTime",
      "userInfo",
      "listid",
    ]),
  },
  methods: {
    ...mapMutations(["getUserInfo"]),
  },
};
</script>

<style lang="less" scoped>
.playBar {
  position: fixed;
  left: 10px;
  bottom: 10px;
  width: 57.8px;
  height: 57.8px;
  box-shadow: 2px 2px 10px 0 rgb(0 0 0 / 30%);
  border-radius: 50%;
  background-color: #fff;
  z-index: 99999;

  .img {
    border-radius: 50%;
    position: absolute;
    top: 3%;
    left: 3%;
    width: 94%;
    height: 94%;
    overflow: hidden;
    @keyframes barRotate {
      0% {
        transform: rotate(0deg);
      }
      100% {
        transform: rotate(360deg);
      }
    }
    animation: barRotate 9s linear infinite;
    animation-play-state: paused;
    &.active {
      animation-play-state: running;
    }
  }
}
</style>