<template>
  <div class="homeSheet" v-if="sheet?.voiceDataList?.length">
    <router-link
      :to="{
        path: '/item',
        query: { id: sheet.playSheetInfo.id },
      }"
    >
      <h3 class="sheetTitle">
        <span>{{ sheet.playSheetInfo.name }}</span>
        <i></i>
      </h3>
    </router-link>
    <div class="sheetList">
      <div
        class="sheetItem"
        v-for="sheetItem in sheet.voiceDataList"
        :key="sheetItem.voiceId"
      >
        <van-image
          :src="sheetItem.cover"
          width="96"
          lazy-load
          height="96"
          radius="4"
        >
          <!-- <template v-slot:loading>
            <van-loading type="spinner" size="20" />
          </template> -->
        </van-image>
        <!-- <img :src="sheetItem.cover" alt="" class="sheetCover" /> -->
        <div class="mask">
          <div class="playIcon">
            <img
              src="../assets/maskPlayIcon.png"
              alt=""
              v-if="
                !(maskIsPlay && sheetItem.voiceId === maskPlaySheet.voiceId)
              "
              @click="
                maskPlay();
                showPlayBay();
                getMaskPlaySheet(sheetItem);
                getPlayList(sheet.voiceDataList);
                getPlayListId(sheet.playSheetInfo.id);
              "
            />
            <img
              src="../assets/maskPauseIcon.png"
              alt=""
              v-else
              @click="
                maskUnplay();
                getMaskPlaySheet(sheetItem);
              "
            />
          </div>
          <div class="playCount">
            <i></i>
            <span>{{
              sheetItem.playCount > 10000
                ? sheetItem.playCount > 100000000
                  ? (sheetItem.playCount / 100000000).toFixed(1) + "亿"
                  : (sheetItem.playCount / 10000).toFixed(1) + "万"
                : sheetItem.playCount
            }}</span>
          </div>
        </div>
        <router-link
          :to="{
            path: '/play',
            query: { id: sheetItem.voiceId, listid: sheet.playSheetInfo.id },
          }"
          class="itemName"
          :style="{
            color: sheetItem?.voiceId === maskPlaySheet?.voiceId ? 'red' : '',
          }"
          >{{ sheetItem.name }}</router-link
        >
      </div>
      <div
        class="checkall"
        @click="
          $router.push({
            path: '/item',
            query: { id: sheet.playSheetInfo.id },
          })
        "
      >
        <div class="music-icon">
          <i></i>
          <span>{{ sheet.playSheetInfo.voiceNum }}</span>
        </div>
        <div class="check-text">
          <span>查看全部</span>
          <i></i>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  props: {
    sheet: Object,
  },
  data() {
    return {
      // sheetPlay: false,
    };
  },
  computed: {
    ...mapState(["maskIsPlay", "maskPlaySheet"]),
  },
  methods: {
    ...mapMutations([
      "maskPlay",
      "maskUnplay",
      "getMaskPlaySheet",
      "showPlayBay",
      "getPlayList",
      "getPlayListId",
    ]),
  },
};
</script>

<style lang="less" scoped>
.homeSheet {
  a {
    text-decoration: none;
    color: #333;
    h3.sheetTitle {
      padding: 24px 6px 16px 16px;
      font-size: 18px;
      font-weight: 700;
      display: flex;
      justify-content: space-between;
      align-items: center;
      span {
        width: 100%;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }
      i {
        // font-size: 19px;
        // color: #a09f9f;
        // font-weight: 200;
        display: block;
        width: 19px;
        height: 19px;
        background: url("../assets/right.png") no-repeat;
        background-size: 10px;
        background-position: center;
      }
    }
  }
  .sheetList {
    display: flex;
    overflow-x: scroll;
    padding-left: 16px;

    height: 155px;
    &::-webkit-scrollbar {
      display: none;
    }
    .sheetItem {
      flex-shrink: 0;
      width: 96px;
      height: 140px;
      margin-right: 16px;
      position: relative;
      .mask {
        position: absolute;
        top: 0;
        left: 0;
        width: 96px;
        height: 96px;
        border-radius: 4px;
        display: flex;
        justify-content: center;
        align-items: center;
        .playIcon {
          width: 40px;
          height: 40px;
          border-radius: 50%;
          display: flex;
          justify-content: center;
          align-items: center;
          img {
            width: 8.5vw;
          }
        }
        .playCount {
          position: absolute;
          bottom: 0;
          left: 0;
          background: linear-gradient(180deg, transparent, rgba(0, 0, 0, 0.5));
          color: #fff;
          font-size: 10px;
          width: 100%;
          display: flex;
          justify-content: flex-start;
          align-items: center;
          height: 20px;
          padding-left: 3px;
          i {
            display: block;
            width: 12px;
            height: 12px;
            background-image: url("../assets/countIcon.png");
            background-repeat: no-repeat;
            background-position: center;
            background-size: 12px;
            margin-right: 3px;
          }
        }
      }
      a.itemName {
        width: 91px;
        // height: 40px;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        font-size: 14px;
        color: #333;
        font-weight: 500;
      }
    }
    .checkall {
      flex-shrink: 0;
      width: 96px;
      height: 96px;
      margin-right: 16px;
      position: relative;
      border-radius: 4px;
      // background-color: aqua;
      padding-left: 10px;
      padding-top: 20px;
      background-image: url("../assets/more_bg.9b1d8e8.png");
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
      .music-icon {
        height: 40px;
        display: flex;
        align-items: center;
        border-bottom: 1px solid hsla(0, 0%, 100%, 0.2);
        font-size: 16px;
        color: #fff;
        i {
          display: block;
          width: 25px;
          height: 25px;
          margin-right: 2.5px;
          background: url("../assets/list.png") no-repeat;
          background-size: 18px;
          background-position: center;
        }
        span {
          // font-weight: bold;
          font-size: 16px;
        }
      }
      .check-text {
        margin-top: 6.5px;
        display: flex;
        align-items: center;
        font-size: 14px;
        color: #fff;
        // span {
        // }
        i {
          display: block;
          width: 18.5px;
          height: 18.5px;
          background: url("../assets/right_arrow.png");
          background-repeat: no-repeat;
          background-position: center;
          background-size: 10px;
        }
      }
    }
  }
}
</style>