<template>
  <div class="userAllVoiceList" v-if="allVoice">
    <router-link to="/">
      <div class="itemImg">
        <div class="maskPlayIcon">
          <div class="inner">
            <img
              src="../assets/maskPlayIcon.png"
              alt=""
              v-if="!(maskIsPlay && allVoice?.id === maskPlaySheet?.id)"
              @click.prevent="
                maskPlay();
                showPlayBay();
                getMaskPlaySheet(allVoice);
                getPlayList(userAllVoice);
              "
            />
            <img
              src="../assets/maskPauseIcon.png"
              alt=""
              v-else
              @click.prevent="
                maskUnplay();
                getMaskPlaySheet(allVoice);
              "
            />
          </div>
        </div>
        <van-image
          :src="
            allVoice.cover
              ? imgUrl + allVoice.cover
              : userInfo.cdnPortrait + userInfo.user.portrait
          "
          alt=""
          fit="cover"
          lazy-load
        >
          <!-- <template v-slot:loading>
            <van-loading type="spinner" size="20" />
          </template> -->
        </van-image>
        <!-- <img :src="imgUrl + allVoice.cover" alt="" /> -->
      </div>
      <div class="itemInfo">
        <div
          class="infoName"
          :style="{
            color: allVoice?.id === maskPlaySheet?.id ? 'red' : '',
          }"
        >
          {{ allVoice.name }}
        </div>
        <div class="infoTag">
          <span>
            {{
              Math.floor(allVoice.duration / 60) > 0
                ? Math.floor(allVoice.duration / 60) > 9
                  ? Math.floor(allVoice.duration / 60)
                  : "0" + Math.floor(allVoice.duration / 60)
                : "00"
            }}'{{
              allVoice.duration % 60 > 9
                ? allVoice.duration % 60
                : "0" + (allVoice.duration % 60)
            }}"</span
          >
          <span>
            {{
              allVoice.stats.play > 10000
                ? (allVoice.stats.play / 10000).toFixed(1) + "万"
                : allVoice.stats.play
            }}
            播放</span
          >
          <span>
            {{
              allVoice.stats.commentCount > 10000
                ? (allVoice.stats.commentCount / 10000).toFixed(1) + "万"
                : allVoice.stats.commentCount
            }}
            评论</span
          >
        </div>
      </div>
    </router-link>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  props: {
    allVoice: Object,
    imgUrl: String,
    // userInfo: Object,
    userAllVoice: Array,
  },
  computed: {
    ...mapState(["maskIsPlay", "maskPlaySheet", "userInfo"]),
  },
  methods: {
    ...mapMutations([
      "maskPlay",
      "maskUnplay",
      "getMaskPlaySheet",
      "showPlayBay",
      "getPlayList",
    ]),
  },
};
</script>

<style lang="less" scoped>
.userAllVoiceList {
  width: 100%;
  background-color: #fff;
  &:first-child {
    margin-top: 8px;
    padding-top: 32px;
  }
  a {
    height: 90px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    &::after {
      content: "";
      display: block;
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 1px;
      z-index: 10000;
      transform: scaleY(0.5);
      background: #dbdbdb;
    }
    .itemImg {
      position: relative;
      width: 60px;
      height: 60px;
      border-radius: 6px;
      overflow: hidden;
      box-shadow: 3px 3px 10px rgb(0 0 0 / 5%);
      .maskPlayIcon {
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 999;
        .inner {
          width: 40px;
          height: 40px;
          border-radius: 50%;
          display: flex;
          justify-content: center;
          align-items: center;
          // img {
          //   // width: 31.89px;
          // }
        }
      }
    }
    .itemInfo {
      padding-left: 10px;
      text-align: left;
      flex: 1;
      .infoName {
        font-size: 15px;
        padding-bottom: 9px;
        color: #333;
      }
      .infoTag {
        color: #000;
        opacity: 0.3;
        font-size: 14px;
        span {
          display: inline-block;
          height: 20px;
          margin-right: 10px;
        }
      }
    }
  }
}
</style>