<template>
  <div class="userPlayList">
    <div class="list">
      <router-link
        :to="{
          path: '/item',
          query: { id: userPlayItem?.id },
        }"
        v-for="userPlayItem in userPlayList"
        :key="userPlayItem.id"
      >
        <div class="itemImg">
          <van-image
            :src="
              userPlayItem.cover ? userPlayItem.cover : userPlayItem.ownerCover
            "
            lazy-load
            width="60"
            height="60"
            radius="6"
            fit="cover"
          >
            <!-- <template v-slot:loading>
              <van-loading type="spinner" size="20" />
            </template> -->
          </van-image>

          <!-- <img
            :src="
              userPlayItem.cover ? userPlayItem.cover : userPlayItem.ownerCover
            "
            alt=""
          /> -->
        </div>
        <div class="itemDesc">
          <div class="itemName">{{ userPlayItem.name }}</div>
          <div class="itemTag">
            <span>{{ format(userPlayItem.createTime) }}</span>
            <span>
              <i></i>
              <strong>{{ userPlayItem.voiceNum }}</strong>
            </span>
          </div>
        </div>
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    userPlayList: Array,
  },
  methods: {
    add(m) {
      return m < 10 ? "0" + m : m;
    },
    format(ntime) {
      var time = new Date(ntime * 1000);
      var year = time.getFullYear();
      var month = time.getMonth() + 1;
      var day = time.getDate();
      return year + "-" + this.add(month) + "-" + this.add(day);
    },
  },
};
</script>

<style lang="less" scoped>
.userPlayList {
  .list {
    margin-top: 5px;
    width: 100%;
    padding: 0 16px;
    a {
      height: 90px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      position: relative;
      text-decoration: none;
      color: #333;
      &::after {
        content: "";
        display: block;
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 1px;
        z-index: 10000;
        transform: scaleY(0.5);
        background: #dbdbdb;
      }
      .itemImg {
        width: 60px;
        height: 60px;
        border-radius: 6px;
        overflow: hidden;
        box-shadow: 3px 3px 10px rgb(0 0 0 / 5%);
        // img {
        //   width: 60px;
        //   height: 60px;
        //   border-radius: 6px;
        // }
      }
      .itemDesc {
        flex: 1;
        padding-left: 10px;
        text-align: left;
        .itemName {
          font-size: 15px;
          padding-bottom: 9px;
          color: #333;
        }
        .itemTag {
          color: #000;
          opacity: 0.3;
          span {
            display: inline-block;
            height: 20px;
            margin-right: 10px;
            font-size: 14px;
            i {
              display: inline-block;
              width: 17.5px;
              height: 17.6px;
              background-image: url("../assets/userPlayList.png");
              background-size: 11px;
              background-repeat: no-repeat;
              background-position: center;
              position: relative;
              top: 4px;
            }
            strong {
              height: 20px;
              display: inline-block;
              font-weight: 400;
            }
          }
        }
      }
    }
  }
}
</style>