<template>
  <div class="typeView">
    <div class="typehead">
      <span
        v-for="kind in filterTypes"
        :key="kind.type"
        :style="filtertype == kind.type ? 'color:#fe5353;' : ''"
        @click="(filtertype = kind.type), savetype(filtertype)"
        >{{ kind.text }}</span
      >
    </div>
    <ul class="typelist">
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        :immediate-check="false"
        @load="onLoad($route.query.typeName, filtertype)"
      >
        <TypeList
          v-for="(typedata, index) in list"
          :key="index"
          :list="list"
          :typedata="typedata"
        ></TypeList>
        <!-- @click.native="
            see(typedata);
            getPlayList(typedatas);
            getPlayVoice(typedata);
            toggleplaying();
          " -->
      </van-list>
    </ul>
  </div>
</template>

<script>
import TypeList from "@/components/TypeList.vue";
export default {
  components: {
    TypeList,
  },
  data() {
    return {
      filterTypes: [
        { type: "smart", text: "智能排序" },
        { type: "playCount", text: "最多收听" },
        { type: "uploadTime", text: "最新上传" },
      ],
      filtertype: JSON.parse(localStorage.getItem("filtertype")) || "smart",

      page: 0,
      totle: 0,

      list: [],
      loading: false,
      finished: false,
    };
  },
  methods: {
    savetype(type) {
      // console.log(type);
      localStorage.setItem("filtertype", JSON.stringify(type));
    },
    getType(text, type) {
      this.page = 1;
      this.totle = 20;
      // this.finished = false;
      this.axios
        .get("https://m.lizhi.fm/vodapi/m5/program", {
          params: {
            label: text,
            sort: type,
            page: 0,
            count: 30,
          },
        })
        .then((res) => {
          // console.log(res);
          this.list = res.data.data;
        });
    },
    onLoad(text, type) {
      this.page++;
      this.axios
        .get("https://m.lizhi.fm/vodapi/m5/program", {
          params: {
            label: text,
            sort: type,
            page: this.page,
            count: 30,
          },
        })
        .then((res) => {
          // console.log(res.data.data);
          this.totle += res.data.data.length;
          this.list = [...this.list, ...res.data.data];
          if (!res.data.data.length) {
            this.finished = true;
          }
          this.loading = false;
        });
      // .catch(() => {
      //   this.error = true;
      // });
    },
  },
  mounted: function () {
    this.getType(this.$route.query.typeName, this.filtertype);
  },
  beforeRouteUpdate(to, from, next) {
    this.getType(to.query.typeName, this.filtertype);
    next();
  },
  watch: {
    filtertype(filtertype) {
      this.getType(this.$route.query.typeName, filtertype);
      // this.onLoad(this.$route.query.kindName, filtertype);
    },
  },
};
</script>

<style lang="less" scoped>
.typeView {
  .typehead {
    height: 44px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    span {
      font-size: 12px;
      width: 80px;
      display: flex;
      align-items: center;
      justify-content: center;
      color: rgba(0, 0, 0, 0.3);
      &:nth-of-type(2) {
        border-left: 1px solid #e1e1e1;
        border-right: 1px solid #e1e1e1;
      }
    }
  }
}
</style>