<template>
  <li class="similarItem">
    <template v-if="typedata.btnTitle">
      <div class="pic_wrap">
        <img :src="typedata.voiceInfo.imageUrl" alt="" />
        <div class="pic_play">
          <img
            src="../assets/maskPlayIcon.png"
            alt=""
            v-if="
              !(
                maskIsPlay &&
                typedata?.voiceInfo?.voiceId ===
                  maskPlaySheet?.voiceInfo?.voiceId
              )
            "
            @click.prevent="
              maskPlay();
              showPlayBay();
              getMaskPlaySheet(typedata);
            "
          />
          <!-- getPlayList(sheetListVoices); -->
          <img
            src="../assets/maskPauseIcon.png"
            alt=""
            v-else
            @click.prevent="
              maskUnplay();
              getMaskPlaySheet(typedata);
            "
          />
        </div>
      </div>
      <div class="info-desc">
        <h3
          class="dots"
          :style="{
            color:
              typedata?.voiceInfo?.voiceId === maskPlaySheet?.voiceInfo?.voiceId
                ? 'red'
                : '',
          }"
        >
          {{ typedata.voiceInfo.name }}
        </h3>
        <p
          class="dots"
          :style="{
            color:
              typedata?.voiceInfo?.voiceId === maskPlaySheet?.voiceInfo?.voiceId
                ? 'red'
                : '',
          }"
        >
          {{ typedata.userInfo.name }}
        </p>
        <p class="opt">
          <span
            >{{
              Math.floor(typedata.voiceInfo.duration / 60) > 0
                ? Math.floor(typedata.voiceInfo.duration / 60) > 9
                  ? Math.floor(typedata.voiceInfo.duration / 60)
                  : "0" + Math.floor(typedata.voiceInfo.duration / 60)
                : "00"
            }}'{{
              typedata.voiceInfo.duration % 60 > 9
                ? typedata.voiceInfo.duration % 60
                : "0" + (typedata.voiceInfo.duration % 60)
            }}"</span
          >
          <span
            >{{
              typedata.voiceExProperty.replayCount > 10000
                ? (typedata.voiceExProperty.replayCount / 10000).toFixed(1) +
                  "万"
                : typedata.voiceExProperty.replayCount
            }}播放</span
          >
          <span>评论{{ typedata.voiceExProperty.commentCount }}</span>
          <i :style="index <= 4 ? 'display:block;' : 'display:none;'"
            >打开APP收听</i
          >
        </p>
      </div>
    </template>
  </li>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  props: {
    typedata: Object,
    index: Number,
  },
  computed: {
    ...mapState(["maskIsPlay", "maskPlaySheet"]),
  },
  methods: {
    ...mapMutations([
      "maskPlay",
      "maskUnplay",
      "getMaskPlaySheet",
      "getPlayList",
      "showPlayBay",
    ]),
  },
};
</script>

<style lang="less" scoped>
li {
  width: 100%;
  padding: 12px 16px;
  height: 84px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  background-color: #fff;
  .pic_wrap {
    flex-shrink: 0;
    width: 60px;
    height: 60px;
    border-radius: 8px;
    margin-right: 16px;
    position: relative;
    img {
      display: block;
      width: 100%;
      height: 100%;
      border-radius: 8px;
    }
    .pic_play {
      width: 32px;
      height: 32px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      // background-image: url("../assets/play.png");
      background-size: 30px;
      background-repeat: no-repeat;
      background-position: center;
    }
  }
  .info-desc {
    flex: 1;
    width: 200px;
    // background-color: rgba(0,0,0,.3);
    .dots {
      display: block;
      width: 100%;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
    h3.dots {
      font-size: 14px;
      color: rgba(0, 0, 0, 0.8);
    }
    p.dots {
      margin: 4px 0;
      font-size: 10px;
      color: rgba(0, 0, 0, 0.5);
    }
    .opt {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      margin-top: 5px;
      color: rgba(0, 0, 0, 0.3);
      margin: 4px 0;
      position: relative;
      font-size: 12px;
      span {
        margin-right: 12px;
        display: flex;
        justify-content: flex-start;
        align-items: flex-end;
      }
      i {
        position: absolute;
        right: 0;
        font-size: 12px;
        font-style: normal;
        color: #fe5353;
      }
    }
  }
  .download {
    flex-shrink: 0;
    img {
      margin: 0;
      width: 25px;
      height: 25px;
    }
  }
}
</style>