<template>
  <div class="userHotList">
    <div class="hotListTitle" v-if="userHotVoice.length">
      <div class="playIcon">
        <i></i>
        <span>全部播放</span>
      </div>
    </div>
    <div class="listContent" v-if="userHotVoice.length">
      <div class="list">
        <router-link
          to="#"
          v-for="hotVoice in userHotVoice"
          :key="hotVoice?.voiceInfo?.voiceId"
        >
          <div class="itemImg">
            <div class="maskPlayIcon">
              <div class="inner">
                <img
                  src="../assets/maskPlayIcon.png"
                  alt=""
                  v-if="
                    !(
                      maskIsPlay &&
                      hotVoice?.voiceInfo?.voiceId ===
                        maskPlaySheet?.voiceInfo?.voiceId
                    )
                  "
                  @click.prevent="
                    maskPlay();
                    showPlayBay();
                    getMaskPlaySheet(hotVoice);
                    getPlayList(userHotVoice);
                  "
                />
                <img
                  src="../assets/maskPauseIcon.png"
                  alt=""
                  v-else
                  @click.prevent="
                    maskUnplay();
                    getMaskPlaySheet(hotVoice);
                  "
                />
              </div>
            </div>
            <van-image :src="hotVoice.voiceInfo.imageUrl" lazy-load>
              <!-- <template v-slot:loading>
                <van-loading type="spinner" size="20" />
              </template> -->
            </van-image>
            <!-- <img :src="hotVoice.voiceInfo.imageUrl" alt="" /> -->
          </div>
          <div class="itemInfo">
            <div
              class="infoName"
              :style="{
                color:
                  hotVoice?.voiceInfo?.voiceId ===
                  maskPlaySheet?.voiceInfo?.voiceId
                    ? 'red'
                    : '',
              }"
            >
              {{ hotVoice.voiceInfo.name }}
            </div>
            <div class="infoTag">
              <span>
                {{
                  Math.floor(hotVoice.voiceInfo.duration / 60) > 0
                    ? Math.floor(hotVoice.voiceInfo.duration / 60) > 9
                      ? Math.floor(hotVoice.voiceInfo.duration / 60)
                      : "0" + Math.floor(hotVoice.voiceInfo.duration / 60)
                    : "00"
                }}'{{
                  hotVoice.voiceInfo.duration % 60 > 9
                    ? hotVoice.voiceInfo.duration % 60
                    : "0" + (hotVoice.voiceInfo.duration % 60)
                }}"</span
              >
              <span>
                {{
                  hotVoice.voiceExProperty.replayCount > 10000
                    ? (hotVoice.voiceExProperty.replayCount / 10000).toFixed(
                        1
                      ) + "万"
                    : hotVoice.voiceExProperty.replayCount
                }}
                播放</span
              >
              <span>
                {{
                  hotVoice.voiceExProperty.commentCount > 10000
                    ? (hotVoice.voiceExProperty.commentCount / 10000).toFixed(
                        1
                      ) + "万"
                    : hotVoice.voiceExProperty.commentCount
                }}
                评论</span
              >
            </div>
          </div>
        </router-link>
      </div>
      <div class="hotBtn">
        <div class="hotBtnText">打开APP收听更多热门声音</div>
      </div>
    </div>
    <div class="backColor" v-else>
      <div class="empty">该主播尚未发布内容</div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  props: {
    userHotVoice: Array,
  },
  computed: {
    ...mapState(["maskIsPlay", "maskPlaySheet", "userInfo"]),
  },
  methods: {
    ...mapMutations([
      "maskPlay",
      "maskUnplay",
      "getMaskPlaySheet",
      "showPlayBay",
      "getPlayList",
    ]),
  },
};
</script>

<style lang="less" scoped>
.userHotList {
  .hotListTitle {
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding-top: 10px;
    color: #b2b2b2;
    font-size: 14px;
    position: relative;
    width: 345px;
    margin: 0 auto;
    .playIcon {
      height: 20px;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 16px;
      color: #000;
      font-weight: 400;
      i {
        width: 25px;
        height: 27.2px;
        margin-right: 3px;
        background-image: url("../assets/play_black.png");
        background-repeat: no-repeat;
        background-position: center;
        background-size: 21px;
      }
      //   span {
      //     color: #000;
      //     font-weight: 400;
      //     font-size: 16px;
      //   }
    }
  }
  .listContent {
    .list {
      width: 100%;
      padding: 0 16px;
      a {
        height: 90px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: relative;
        &::after {
          content: "";
          display: block;
          position: absolute;
          bottom: 0;
          left: 0;
          width: 100%;
          height: 1px;
          z-index: 10000;
          transform: scaleY(0.5);
          background: #dbdbdb;
        }
        .itemImg {
          position: relative;
          width: 60px;
          height: 60px;
          border-radius: 6px;
          overflow: hidden;
          box-shadow: 3px 3px 10px rgb(0 0 0 / 5%);
          .maskPlayIcon {
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 999;
            .inner {
              width: 40px;
              height: 40px;
              border-radius: 50%;
              display: flex;
              justify-content: center;
              align-items: center;
              img {
                width: 31.89px;
              }
            }
          }
        }
        .itemInfo {
          padding-left: 10px;
          text-align: left;
          flex: 1;
          .infoName {
            font-size: 15px;
            padding-bottom: 9px;
            color: #333;
          }
          .infoTag {
            color: #000;
            opacity: 0.3;
            font-size: 14px;
            span {
              display: inline-block;
              height: 20px;
              margin-right: 10px;
            }
          }
        }
      }
    }
    .hotBtn {
      padding-bottom: 5px;
      .hotBtnText {
        border-radius: 20px;
        color: #fff;
        background: #fe5656;
        font-weight: 500;
        -webkit-box-shadow: 0 2px 4px 0 rgb(255 98 98 / 30%);
        box-shadow: 0 2px 4px 0 rgb(255 98 98 / 30%);
        width: 275px;
        height: 40px;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 14px;
        margin: 15px auto;
      }
    }
  }
  .backColor {
    width: 100%;
    // height: 100%;
    height: 309.5px;
    background: #f2f2f2;
    margin-top: 16px;
    padding: 16px 0;
    .empty {
      width: 350px;
      height: 150px;
      background: #fff;
      color: #999;
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0 auto;
      font-size: 14px;
      border-radius: 6px;
    }
  }
}
</style>