<template>
  <div class="userVoiceList">
    <div class="tableTitle">
      <div class="itemTitle" @click="toggleList" :class="{ active }">
        热门声音
      </div>
      <div class="itemTitle" @click="toggleList" :class="{ active: !active }">
        播单({{ userPlayList.length }})
      </div>
    </div>
    <!-- 用户热门声音 -->
    <UserHotList :userHotVoice="userHotVoice" v-show="active"></UserHotList>
    <!-- 用户播单 -->
    <UserPlayList :userPlayList="userPlayList" v-show="!active"></UserPlayList>
  </div>
</template>

<script>
import UserHotList from "@/components/UserHotList.vue";
import UserPlayList from "@/components/UserPlayList.vue";
export default {
  props: {
    userHotVoice: Array,
    userPlayList: Array,
  },
  components: {
    UserHotList,
    UserPlayList,
  },
  data() {
    return {
      active: true,
    };
  },
  methods: {
    toggleList: function () {
      this.active = !this.active;
    },
  },
};
</script>

<style lang="less" scoped>
.userVoiceList {
  width: 100%;
  margin-top: 8px;
  padding-top: 16px;
  background-color: #fff;

  .tableTitle {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-left: 16px;
    font-size: 14px;
    .itemTitle {
      width: 70px;
      display: flex;
      justify-content: flex-start;
      color: #999;
      &.active {
        font-weight: 700;
        color: #333;
      }
    }
  }
}
</style>