<template>
  <div class="playView" v-if="nowPlay && cardData && playuserinfo">
    <header>
      <a class="icon" href="/"
        ><img src="../assets/appHeadIcon.png" alt="" class="headIcon"
      /></a>
      <div class="headInfo">
        <img src="../assets/appHeadLogoText.png" alt="" class="logoText" />
        <div class="infoText">听音乐小说广播剧的有声社区</div>
      </div>
      <div class="downLoadBtn">打开APP</div>
    </header>
    <div class="playCard">
      <div class="cardCD">
        <van-image
          :src="nowPlay.cover"
          width="100%"
          height="100%"
          lazy-load
          fit="cover"
        >
        </van-image>
        <div class="playCardControl">
          <div
            class="pre"
            @click="
              $router.replace({
                path: '/play',
                query: {
                  id: prePlay.voiceId,
                  listid: $route.query.listid,
                },
              });
              $router.go(0);
            "
          ></div>
          <div class="start">
            <i
              v-if="
                !(maskIsPlay && nowPlay?.voiceId === maskPlaySheet?.voiceId)
              "
              @click.prevent="
                maskPlay();
                showPlayBay();
                getMaskPlaySheet(nowPlay);
                getPlayList(cardData.voices);
              "
            ></i>
            <i
              class="pause"
              v-else
              @click.prevent="
                maskUnplay();
                getMaskPlaySheet(nowPlay);
              "
            ></i>
          </div>
          <div
            class="next"
            @click="
              $router.replace({
                path: '/play',
                query: {
                  id: nextPlay.voiceId,
                  listid: $route.query.listid,
                },
              });
              $router.go(0);
            "
          ></div>
        </div>
      </div>
      <h1 class="cardName">{{ nowPlay.name }}</h1>
      <div class="voiceControl">
        <div class="voiceTime">
          {{ draping === false ? playtime(currentTime) : playtime(updatetime) }}
        </div>
        <label class="bar">
          <input
            type="range"
            class="barcorl"
            min="0"
            :max="nowPlay?.duration"
            :value="changeTime"
            @input="
              draping = true;
              $nextTick(function () {
                updateBar($event.target.value, nowPlay?.duration);
              });
              updatetime = $event.target.value;
            "
            @change="
              draping = false;
              changecurrentTime($event.target.value);
              $nextTick(function () {
                updateBar($event.target.value, nowPlay?.duration);
              });
            "
          />
          <div class="barcontrol" ref="barcontrol"></div> </label
        >`
        <div class="voiceTime">{{ playtime(nowPlay?.duration) }}</div>
      </div>
      <div class="processControl">
        <div class="controlList">
          <div class="controlItem">
            <img src="../assets/moveBack.png" alt="" @click="timeprev()" />
          </div>
          <div class="controlItem">
            <img src="../assets/downLoad.png" alt="" />
          </div>
          <div class="controlItem">
            <img src="../assets/processSpeed.png" alt="" />
          </div>
          <div class="controlItem">
            <img src="../assets/moveTo.png" alt="" @click="timenext()" />
          </div>
        </div>
      </div>
      <div class="downLoadBtn">打开APP收藏下载</div>
    </div>
    <div class="playCardUserInfo">
      <div class="title">声音详情</div>
      <div class="desc">
        <div class="wrap">
          <img
            :src="playuserinfo.userVoice.userInfo.photo"
            alt=""
            @click="
              $router.push({
                path: '/user',
                query: {
                  userId: playuserinfo.userVoice.userInfo.userId,
                },
              })
            "
          />
          <div class="userInfo">
            <div
              class="userName"
              @click="
                $router.push({
                  path: '/user',
                  query: {
                    userId: playuserinfo.userVoice.userInfo.userId,
                  },
                })
              "
            >
              {{ playuserinfo.userVoice.userInfo.name }}
            </div>
            <div class="detail">
              FM {{ playuserinfo.userVoice.userInfo.band }}
              {{
                playuserinfo.userVoice.userInfo.fansCnt > 10000
                  ? (playuserinfo.userVoice.userInfo.fansCnt / 10000).toFixed(
                      1
                    ) + "万"
                  : playuserinfo.userVoice.userInfo.fansCnt
              }}粉丝
            </div>
          </div>
        </div>
        <!-- <div class="addFocus">
          <div class="focusText"></div>
        </div> -->
      </div>
      <div class="voiceInfo" v-if="playuserinfo.userVoice.voiceInfo">
        <h3 class="name">{{ playuserinfo.userVoice.voiceInfo.name }}</h3>
        <div class="info">
          {{ format(playuserinfo.userVoice.voiceInfo.createTime) }} 播放
          {{ numchuli(playuserinfo.userVoice.voiceExProperty.replayCount) }}
        </div>
        <div class="detail" :class="{ actived }">
          <i
            @click="actived = !actived"
            v-if="playuserinfo.userVoice.voiceDetailProperty.text.length > 20"
          ></i>
          <div class="content">
            {{ playuserinfo.userVoice.voiceDetailProperty.text }}
          </div>
        </div>
      </div>
    </div>

    <ul class="similarlist">
      <h3 class="title">猜你喜欢听</h3>
      <SimilarItem
        v-for="(typedata, index) in similardatas"
        :key="index"
        :typedata="typedata"
        :index="index"
      />
      <!-- @click.native="
          getPlayList(similardatas);
          getPlayVoice(typedata);
          toggleplaying();
        " -->
    </ul>
    <ul class="voicelist">
      <h3 class="title">大家都在听</h3>
      <VoiceListen
        v-for="(typedata, index) in voicedatas"
        :key="index"
        :typedata="typedata"
        :index="index"
      />
      <!-- @click.native="
          getPlayList(voicedatas);
          getPlayVoice(typedata);
          toggleplaying();
        " -->
      <div class="down_btn">打开APP 收听更多热播声音</div>
    </ul>
    <ul class="commentlist" v-if="commentdatas?.length">
      <h3 class="title">热门评论</h3>
      <HotComment
        v-for="(commentdata, index) in commentdatas"
        :key="index"
        :commentdata="commentdata"
      />
      <div class="app_open">
        <div class="go_tip">打开APP查看全部评论&nbsp;&nbsp;></div>
      </div>
    </ul>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import SimilarItem from "@/components/SimilarItem.vue";
import VoiceListen from "@/components/VoiceListen.vue";
import HotComment from "@/components/HotComment.vue";
export default {
  components: {
    SimilarItem,
    VoiceListen,
    HotComment,
  },
  data() {
    return {
      cardData: null,
      nowPlay: null,
      //   请求参数
      num: 10,
      page: 1,

      // 进度条
      changeTime: this?.currentTime,
      draping: false,
      updatetime: 0,

      playuserinfo: null,

      // 用户信息展开
      actived: false,
      // 猜你喜欢
      similardatas: [],
      // 大家都在听
      voicedatas: [],
      // 评论
      commentdatas: [],
    };
  },
  computed: {
    ...mapState(["currentTime", "maskIsPlay", "maskPlaySheet"]),
    // 当前歌曲在列表中的下标

    nowIndex: function () {
      return this.cardData.voices.findIndex(
        (o) => o.voiceId === this.nowPlay?.voiceId
      );
    },
    // 下一首歌的信息
    nextPlay: function () {
      if (this.nowIndex === this.cardData.voices.length - 1) {
        return this.cardData.voices[0];
      } else {
        return this.cardData.voices[this.nowIndex + 1];
      }
    },
    // 上一首歌的信息
    prePlay: function () {
      if (this.nowIndex === 0) {
        return this.cardData.voices[this.cardData.voices.length - 1];
      } else {
        return this.cardData.voices[this.nowIndex - 1];
      }
    },
  },
  methods: {
    ...mapMutations([
      "getMaskPlaySheet",
      "getPlayList",
      "changecurrentTime",
      "maskPlay",
      "maskUnplay",
      "showPlayBay",
      "timenext",
      "timeprev",
    ]),
    getItemData: function () {
      if (this.$route.query.listid) {
        this.axios
          .get("https://m.lizhi.fm/vodapi/playsheet/data", {
            params: {
              id: this.$route.query.listid,
              page: this.page,
              count: this.num,
            },
          })
          .then((res) => {
            this.cardData = res.data;
            this.nowPlay = res.data.voices.filter(
              (o) => o.voiceId === this.$route.query.id
            )[0];
            this.$nextTick(function () {
              this.getMaskPlaySheet(this.nowPlay);
            });
          });
      }
    },
    // 用户信息
    getPlayUserInfo: function () {
      this.axios
        .get(`https://m.lizhi.fm/vodapi/voice/info/${this.$route.query.id}`)
        .then((res) => {
          this.playuserinfo = res.data.data;
          // this.changeTablists(this.tablistes);
        });
    },
    // 猜你喜欢
    getGuesslike: function () {
      this.axios
        .get(`https://m.lizhi.fm/vodapi/voice/similar/${this.$route.query.id}`)
        .then((res) => {
          // console.log(res.data.data);
          // this.guesslikevoic = res.data.data;
          this.similardatas = res.data.data;
        });
    },
    // 大家都在听
    getlisten: function () {
      this.axios
        .get("https://m.lizhi.fm/vodapi/voice/rank/voice")
        .then((res) => {
          // console.log(res.data.data);
          this.voicedatas = res.data.data;
        });
    },
    // 评论
    getcomment: function () {
      this.axios
        .get(`https://m.lizhi.fm/vodapi/voice/comment/${this.$route.query.id}`)
        .then((res) => {
          // console.log(res.data.data);
          this.commentdatas = res.data.data;
        });
    },

    // 分秒转换
    playtime(time) {
      let m = Math.floor(time / 60);
      let s = Math.floor(time % 60);
      if (m < 10) {
        m = "0" + m;
      }
      if (s < 10) {
        s = "0" + s;
      }
      return m + ":" + s;
    },
    // 进度条更新
    updateBar(timing, timed) {
      // console.log("更新进度");
      let barlength = Math.ceil((timing / timed) * 100) + "%";
      // console.log(barlength,timing,timed);
      if (this.$refs.barcontrol.style) {
        this.$nextTick(function () {
          // console.log(this.$refs.barcontrol);
          this.$refs.barcontrol.style.width = barlength;
        });
      }
    },
    // 时间戳转日期
    add(m) {
      return m < 10 ? "0" + m : m;
    },
    format(ntime) {
      var time = new Date(ntime * 1000);
      var year = time.getFullYear();
      var month = time.getMonth() + 1;
      var day = time.getDate();
      return year + "-" + this.add(month) + "-" + this.add(day);
    },
    // 进制补全
    numchuli(num) {
      return num > 10000
        ? num > 100000000
          ? (num / 100000000).toFixed(1) + "亿"
          : (num / 10000).toFixed(1) + "万"
        : num;
    },
  },
  mounted() {
    this.getItemData();
    this.getPlayUserInfo();
    this.getGuesslike();
    this.getlisten();
    this.getcomment();
  },
  // updated() {
  //   this.$nextTick(function () {
  //     // console.log(this.$refs, "更新");
  //     this.updateBar(this.changeTime, this.nowPlay?.duration);
  //   });
  // },
  beforeUpdate() {
    this.$nextTick(function () {
      // console.log(this.changeTime);
      // this.$refs.barcontrol.style.width = Math.ceil((timing / timed) * 100) + "%"
    });
  },
  watch: {
    currentTime(currentTime) {
      // console.log(this.draping);
      if (!this.draping) {
        this.$nextTick(function () {
          this.updateBar(currentTime, this.nowPlay?.duration);
        });
      }
      this.changeTime = currentTime;
      // this.updateBar(this.changeTime, this.duration);
    },
    updatetime(updatetime) {
      this.changeTime = updatetime;
    },
    changeTime: function () {
      this.$nextTick(function () {
        this.updateBar(this.changeTime, this.nowPlay?.duration);
      });
    },
  },
};
</script>

<style lang="less" scoped>
.playView {
  background: #f2f2f2;

  header {
    position: fixed;
    top: 0;

    .top {
      letter-spacing: normal;
    }
  }

  .playCard {
    position: relative;
    padding: 16px 0 9px;
    display: block;
    width: 100%;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    .cardCD {
      width: 226px;
      height: 226px;
      border-radius: 7px;
      overflow: hidden;
      box-shadow: 2px 2px 20px 4px rgb(0 0 0 / 13%);
      position: relative;

      .playCardControl {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.3);
        z-index: 100;
        display: flex;
        justify-content: center;
        align-items: center;
        // font-size: 50;
        color: #fff;

        .pre {
          background-image: url("../assets/previous.png");
          width: 50px;
          height: 55.2px;
          background-size: 30px;
          background-repeat: no-repeat;
          background-position: center;
        }

        .start {
          font-size: 1.3rem;
          display: flex;
          justify-content: center;
          align-items: center;
          color: #fff;
          margin: 0 20px;
          width: 65px;
          height: 50px;

          i {
            width: 65px;
            height: 70.4px;
            background-image: url("../assets/play_circle.png");
            background-size: 55px;
            background-repeat: no-repeat;
            background-position: center;

            &.pause {
              background-image: url("../assets/pause_circle.png");
            }
          }
        }

        .next {
          background-image: url("../assets/previous.png");
          transform: rotate(180deg);
          width: 50px;
          height: 55.2px;
          background-size: 30px;
          background-repeat: no-repeat;
          background-position: center;
        }
      }
    }

    .cardName {
      font-size: 17px;
      font-weight: 700;
      color: #333;
      margin: 25px auto 15px;
      line-height: 20px;
      width: 300px;
      height: 20px;
      text-align: center;
      overflow: hidden;
      white-space: nowrap;

      @keyframes scrollName {
        0% {
          // transform: translateX(0);
          text-indent: 2em;
        }

        100% {
          // transform: translateX(30%);
          text-indent: -8em;
        }
      }

      animation: scrollName 10s linear infinite;
    }

    .voiceControl {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 40px;
      width: 350px;

      .voiceTime {
        width: 34px;
        margin: 0 16px;
        font-size: 12px;
        color: rgba(0, 0, 0, 0.5);
      }

      .bar {
        width: 100%;
        height: 3px;
        border-radius: 5px;
        background-color: #e5e5e5;
        // background-color: hsla(0, 0%, 100%, 0.2);
        position: relative;

        .barcorl {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 3px;
          opacity: 0;
          top: 50%;
          transform: translateY(-50%);
        }

        .barcontrol {
          width: 0%;
          height: 100%;
          border-radius: 5px;
          background-color: #fe5353;
          position: relative;
          pointer-events: none;

          &::after {
            position: absolute;
            content: "";
            right: -2.5px;
            top: -2.5px;
            width: 8px;
            height: 8px;
            background-color: #fff;
            border-radius: 50%;
            pointer-events: none;
            box-shadow: 0 0 0 4px #fe5353;
          }
        }
      }
    }

    .processControl {
      width: 100%;

      .controlList {
        width: 100%;
        margin: 8px auto;
        display: flex;
        justify-content: center;
        align-items: center;

        .controlItem {
          width: 24px;
          margin: 0 21px;

          img {
            width: 100%;
          }
        }
      }
    }

    .downLoadBtn {
      border-radius: 20px;
      color: #fff;
      background: #fe5656;
      font-weight: 500;
      -webkit-box-shadow: 0 2px 4px 0 rgb(255 98 98 / 30%);
      box-shadow: 0 2px 4px 0 rgb(255 98 98 / 30%);
      width: 275px;
      height: 40px;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 14px;
      margin: 15px auto;
    }
  }

  .playCardUserInfo {
    background: #fff;
    margin-top: 10px;
    margin-bottom: 10px;
    padding-bottom: 16px;

    .title {
      height: 40px;
      display: flex;
      align-items: center;
      padding-left: 20px;
      opacity: 0.3;
      color: #000;
      font-size: 14px;
    }

    .desc {
      padding: 0 20px;
      display: flex;
      justify-content: space-between;
      height: 56px;
      align-items: center;

      .wrap {
        flex: 1;
        width: 0;
        display: flex;
        justify-content: space-between;
        height: 56px;
        align-items: center;

        img {
          flex: 0 0 40px;
          width: 40px;
          height: 40px;
          border-radius: 50%;
        }

        .userInfo {
          margin-left: 10px;
          margin-right: 5px;
          flex: 1;
          width: 0;
          text-align: left;

          .userName {
            font-size: 15px;
            color: #000;
            margin-top: 1px;
            display: block;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            word-break: break-all;
          }

          .detail {
            font-size: 13px;
            color: rgba(0, 0, 0, 0.3);
          }
        }
      }
    }

    .voiceInfo {
      padding-left: 20px;
      padding-right: 20px;

      h3.name {
        font-size: 15px;
        opacity: 0.8;
        padding-bottom: 4px;
        margin-top: 5px;
        line-height: 1.6;
        font-weight: 700;
      }

      .info {
        font-size: 13px;
        color: rgba(0, 0, 0, 0.3);
        padding-bottom: 16px;
        line-height: 1;
      }

      .detail {
        width: 100%;
        position: relative;
        margin: 0;

        &.actived {
          margin: 14px 0;

          i {
            transform: rotate(0deg);
          }

          .content {
            white-space: pre-wrap;
            word-wrap: break-word;
          }
        }

        i {
          position: absolute;
          display: block;
          top: 0;
          right: 0;
          width: 20px;
          height: 20px;
          background-image: url("../assets/top.png");
          background-size: 12px;
          background-position: center;
          background-repeat: no-repeat;
          transform: rotate(180deg);
        }

        .content {
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          font-size: 13px;
          color: #999;
          line-height: 1.8;
          padding-right: 25px;
          margin: 0;
        }
      }
    }
  }

  .similarlist {
    .title {
      padding-left: 20px;
      padding-top: 6px;
      font-size: 14px;
      color: #000;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      // height: 36px;
      background-color: #fff;
      color: #b3b3b3;
    }
  }

  .voicelist {
    background: #fff;
    margin-top: 16px;
    padding-bottom: 10px;

    .title {
      padding-left: 20px;
      padding-top: 6px;
      font-size: 14px;
      color: #000;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      height: 36px;
      background-color: #fff;
      color: #b3b3b3;
    }

    .down_btn {
      width: 275px;
      height: 40px;
      border-radius: 20px;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 14px;
      letter-spacing: 2px;
      color: #fff;
      background: #fe5656;
      font-weight: 500;
      box-shadow: 0 2px 4px 0 rgb(255 98 98 / 30%);
      margin: 15px auto;
    }
  }

  .commentlist {
    background: #fff;
    margin-top: 10px;
    padding: 0 20px;

    .title {
      padding-top: 11px;
      font-size: 14px;
      color: #000;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      height: 36px;
      background-color: #fff;
      color: #b3b3b3;
    }

    .app_open {
      .go_tip {
        border-top: 1px solid #dbdbdb;
        height: 50px;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        color: #999;
        padding-left: 43px;
        font-size: 14px;
      }
    }
  }
}
</style>