<template>
  <div class="itemView" v-if="sheetInfo">
    <header>
      <a class="icon" href="#"
        ><img src="../assets/appHeadIcon.png" alt="" class="headIcon"
      /></a>
      <div class="headInfo">
        <img src="../assets/appHeadLogoText.png" alt="" class="logoText" />
        <div class="infoText">打开APP收藏下载，精彩不容错过</div>
      </div>
      <div class="downLoadBtn top">打开APP</div>
    </header>
    <van-skeleton title avatar :row="20" :loading="loading">
      <!-- {{ this.$route.query.id }} -->
      <!-- <div>实际内容</div> -->
      <div class="sheetInfo">
        <!-- :style="playlist && `background-image: url(${playlist.coverImgUrl})`" -->
        <div
          class="backImg"
          :style="sheetInfo && `background-image:url(${sheetInfo.cover})`"
        ></div>
        <div class="cover">
          <div class="headImg">
            <van-image
              :src="sheetInfo.cover"
              width="100%"
              height="96"
              lazy-load
              fit="cover"
              radius="4"
            >
              <template v-slot:error></template>
            </van-image>
            <!-- <img :src="sheetInfo.cover" alt="" /> -->
            <div class="headShadow1"></div>
            <div class="headShadow2"></div>
          </div>
          <div class="detail">
            <h1 class="infoTitle">{{ sheetInfo.name }}</h1>
            <router-link
              :to="{
                path: '/user',
                query: {
                  userId: sheetInfo.userId,
                },
              }"
              class="userInfo"
            >
              <span class="username">{{ sheetInfo.owner }}</span>
              <i></i>
            </router-link>
          </div>
        </div>
      </div>
      <div class="downLoadBtn">打开APP领取声优福利</div>
      <div class="listTitle">
        <div class="playIcon">
          <i></i>
          <span
            @click="
              $router.push({
                path: '/play',
                query: {
                  id: sheetListVoices[0].voiceId,
                  listid: $route.query.id,
                },
              })
            "
            >全部播放</span
          >
        </div>
        <div class="listCount">（共{{ sheetInfo?.voiceNum }}个声音 ）</div>
      </div>
      <ItemList
        v-for="voice in sheetListVoices"
        :key="voice.voiceId"
        :voice="voice"
        :sheetListVoices="sheetListVoices"
      ></ItemList>

      <div class="moreBtn" v-if="!isShowMore && sheetListVoices.length >= 10">
        <div class="downBtn">
          <img
            src="../assets/downBtn.png"
            alt=""
            @click="
              isShowMore = true;
              num = sheetInfo?.voiceNum;
            "
          />
        </div>
        <div class="moreBtnText">打开APP收听更多声音</div>
      </div>
      <div class="inBottom" v-show="showInBottom">别拉了，到底啦o(╥﹏╥)o</div>
      <!-- <div class="to_top" v-show="showtop" @click="backTop">
        <i></i>
      </div> -->
    </van-skeleton>
  </div>
</template>

<script>
import ItemList from "@/components/ItemList.vue";
export default {
  components: {
    ItemList,
  },
  data() {
    return {
      // 骨架屏状态
      loading: true,
      // 推荐信息
      sheetInfo: null,
      //   列表信息
      sheetListVoices: [],
      //   请求参数
      num: 10,
      page: 1,
      // showtop: false,
      isShowMore: false,

      showInBottom: false,
      // loading: false,
      // finished: false,
    };
  },

  methods: {
    getItemData: function () {
      this.axios
        .get("https://m.lizhi.fm/vodapi/playsheet/data", {
          params: {
            id: this.$route.query.id,
            page: this.page,
            count: this.num,
          },
        })
        .then((res) => {
          // console.log(res.data);
          // if (this.page == 1) {
          this.sheetInfo = res.data.playSheetInfo;
          // }
          this.sheetListVoices = res.data.voices;
          // 加载完成隐藏骨架屏
          this.loading = false;
          if (this.num === this.sheetInfo?.voiceNum) {
            this.showInBottom = true;
          }
        });
    },
  },
  mounted() {
    this.getItemData();
  },

  watch: {
    num: function () {
      // if (this.num === this.sheetInfo.voiceNum) {
      // this.page += 1;
      this.getItemData();
      // }
    },
  },
};
</script>

<style lang="less" scoped>
.itemView {
  header {
    position: fixed;
    top: 0;
    .top {
      letter-spacing: normal;
    }
  }
  .sheetInfo {
    width: 100%;
    height: 148px;
    position: relative;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-left: 16px;
    overflow: hidden;
    background: rgba(0, 0, 0, 0.71);
    .backImg {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 1;
      background-size: 100% 100%;
      filter: blur(20px);
    }
    .cover {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      max-width: 100%;
      height: 100%;
      z-index: 9;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      padding: 15px;
      background: rgba(0, 0, 0, 0.161);
      .headImg {
        width: 96px;
        height: 96px;
        //   border-radius: 50%;
        margin-right: 28px;

        box-shadow: 0 0 5px rgb(0 0 0 / 13%);
        position: relative;
        top: 10px;
        // img {
        //   width: 100%;
        //   height: 96px;
        //   border-radius: 4px;
        // }
        .headShadow1 {
          position: absolute;
          width: 100%;
          height: 100%;
          background-color: hsla(0, 0%, 100%, 0.2);
          border-radius: 4px;
          z-index: -1;
          top: -6px;
          left: 6px;
        }
        .headShadow2 {
          position: absolute;
          width: 100%;
          height: 100%;
          background-color: hsla(0, 0%, 100%, 0.2);
          border-radius: 4px;
          z-index: -1;
          top: -12px;
          left: 12px;
        }
      }
      .detail {
        color: #fff;
        text-shadow: 0 1px 2px rgb(0 0 0 / 10%);
        width: 221px;
        h1 {
          font-size: 18px;
          text-shadow: 0 1px 2px rgb(0 0 0 / 10%);
        }
        a {
          text-decoration: none;
          display: flex;
          margin-top: 4px;
          font-size: 13px;
          align-items: center;
          text-shadow: 0 1px 2px rgb(0 0 0 / 10%);
          color: #fff;
          width: 200px;
          .username {
            //   width: 100%;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
          }
          i {
            width: 15px;
            height: 15.2px;
            background-image: url("../assets/right_arrow.png");
            background-repeat: no-repeat;
            background-size: 7px;
            background-position: center;
          }
        }
      }
    }
  }
  .downLoadBtn {
    width: 275px;
    height: 40px;
    border-radius: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    letter-spacing: 2px;
    color: #fff;
    background: #fe5656;
    font-weight: 500;
    box-shadow: 0 2px 4px 0 rgb(255 98 98 / 30%);
    margin: 15px auto;
  }
  .listTitle {
    width: 345px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    color: #b2b2b2;
    font-size: 14px;
    position: relative;
    margin: 0 auto 10px;
    &::after {
      content: "";
      position: absolute;
      display: block;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 1px;
      background: #dbdbdb;
      z-index: 100;

      transform: scaleY(0.5);
    }
    .playIcon {
      height: 35px;
      display: flex;
      align-items: center;
      justify-content: center;
      color: #000;
      font-weight: 400;
      font-size: 16px;
      i {
        width: 25px;
        height: 25px;
        margin-right: 3px;
        background-image: url("../assets/play_black.png");
        background-repeat: no-repeat;
        background-size: 20px;
        background-position: center;
      }
      //   span {
      //   }
    }
    .listCount {
      height: 35px;
      font-size: 12px;
      display: flex;
      align-items: center;
    }
  }
  .moreBtn {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100px;
    padding-top: 25px;
    position: relative;
    //   z-index: 999;
    .downBtn {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      // z-index: -1;
      background: linear-gradient(0deg, #fff, hsla(0, 0%, 100%, 0.3));
      display: flex;
      justify-content: center;
      align-items: flex-start;
      padding-top: 5px;
      img {
        width: 30px;
      }
    }
    .moreBtnText {
      width: 275px;
      height: 40px;
      border-radius: 20px;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 14px;
      letter-spacing: 2px;
      color: #fff;
      background-color: #fe5656;
      font-weight: 500;
      box-shadow: 0 2px 4px 0 rgb(255 98 98 / 30%);
      margin: 15px auto;
    }
  }
  .inBottom {
    width: 100%;
    height: 100px;
    background: #fff;
    color: #666;
    font-size: 14px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
// .van-image {
